@import '../../styles/index.scss';

.groupPage{
    width: 100vw;
    height: 78vh;
    display: flex;
    background-color: $secondaryColor;
    padding-top: 100px;
    justify-content: center;
}

// Infos Part 

.infos-grp{
    width: 20vw;
    height: 67vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 20px;
    text-align: center;
    border-right: 1px solid $primaryColor;
}
.map{
    height: 250px;
}

.info-grp-description{
    margin-top: 30px;
}
// Chat Part

.chat-part{
    width: 80vw;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.messages{
    width: 50%;
    height: auto;
    color: $secondaryColor;
    text-align: left;
    margin-left: 20px;
}
.message{
    color: black;
    font-size:20px;
    width: 100%;
    height: auto;
    background-color: $primaryColor;
    margin: 20px;
    border-radius: 10px;


}
.message-img{
    display: flex;
    padding-top: 10px;
}

.message-author{
font-size: 1.1em;
padding-bottom: 10px;
font-weight: bold;
}
.message-author-img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 20px;
}

.message-content{
    color: $secondaryColor;
    font-size: 1.1em;
    font-weight: 200;
    margin-left: 30px;
}
.sendAt{
    font-size: 0.6em;
    text-align: right;
    margin-right: 10px;
}

.map{

display: flex;
align-items: center;
justify-content: center;
border: 1px solid black;
}